<template>
  <div class="index" @click="bodyClick">
    <!-- 回到顶部 -->
    <back-top></back-top>
    <el-header class="header">
      <!-- 左侧logo -->
      <h1 :class="['logo', !isCollapse ? 'logo_jpg' : 'logo_png']">后台饮品配置管理</h1>
      <!-- 退出按钮 -->
      <div class="admin_info">
        您好，
        <span class="account_name">{{ userInfo.name }} </span>
        <span class="account_off" @click.stop="showSelect"> 个人中心</span>
        <span class="account_off" @click.stop="accountOff"> 退出</span>
        <transition name="show-log">
          <div class="show_log" v-show="dialogVisible">
            <div @click="editPass">修改密码</div>
            <div>二维码</div>
          </div>
        </transition>
      </div>
    </el-header>
    <el-container class="container">
      <!-- 左侧菜单 -->
      <el-aside :class="[isCollapse ? 'active_path' : '']">
        <div class="toggle-button" @click="toggleCollapse">|||</div>
        <!-- 侧边栏菜单区域 -->
        <el-menu :default-active="$route.path" background-color="#333" text-color="#fff" active-text-color="#1E71B1" :collapse="isCollapse" :collapse-transition="false" router @open="handleOpen" :unique-opened="false" :default-openeds="subTitle">
          <!-- <el-menu :default-active="$route.path" background-color="#333" text-color="#eee" :collapse="isCollapse" :collapse-transition="false" unique-opened router @open="handleOpen"> -->
          <!-- 一级菜单 没有子节点-->
          <div v-for="(item, idx1) in menuList" :key="item.idx1">
            <el-menu-item :index="item.path" v-if="!item.childNode">
              <!-- <i :class="'iconfont icon-' + item.icon"></i> -->
              <img class="iconfont" :src="item.icon" />
              <span slot="title">{{ item.name }}</span>
            </el-menu-item>
            <!-- 一级菜单 有子节点的 -->
            <!-- <el-submenu :index="item.path" v-if="item.childNode" :class="[item.path === activeMenu ? 'aaa' : '']"> -->
            <el-submenu :index="item.path" v-if="item.childNode && item.auth" :class="[item.path === activeMenu ? 'aaa' : '']">
              <!-- 一级模板 -->
              <template slot="title">
                <b :class="item.path === activeMenu ? 'activeBorder' : ''"></b>
                <!-- 一级图标 -->
                <!-- <i :class="'iconfont icon-' + item.icon"></i> -->
                <img class="iconfont" :src="item.icon" />
                <!-- 一级文字 -->
                <span v-show="!isCollapse">{{ item.name }}</span>
              </template>
              <!-- 二级菜单 -->
              <div v-for="(subItem, idx2) in item.childNode" :key="idx2">
                <!-- <el-menu-item :index="subItem.path" @click="saveNavState(item.path, subItem.path)"> -->
                <el-menu-item v-if="subItem.auth" :index="subItem.path" @click="saveNavState(item.path, subItem.path)">
                  <!-- 二级模板 -->
                  <template slot="title">
                    <!-- 二级文字 -->
                    <span :style="{ color: '/' + $route.path.split('/')[1] === subItem.path || $route.path === subItem.path ? '#1E71B1' : '#999' }">{{ subItem.name }}</span>
                  </template>
                </el-menu-item>
              </div>
            </el-submenu>
          </div>
        </el-menu>
      </el-aside>
      <!-- 右侧内容主题 -->
      <el-main :style="{ padding: $route.path === '/home' ? '0' : '20px', width: isCollapse ? 'calc( 100vw - 80px )' : 'calc( 100vw - 320px )' }">
        <!-- 路由占位符 -->
        <router-view></router-view>
      </el-main>
    </el-container>

    <!-- 修改密码对话框 -->
    <el-dialog title="修改密码" :visible.sync="editPassDialog" class="el-dialog-editPass" width="35%">
      <el-divider></el-divider>
      <div class="modal-cont">
        <el-form :model="modifyForm" :rules="changeFormRules" ref="modifyForm" label-width="80px">
          <el-form-item prop="oldPassword" label="原密码">
            <el-input prefix-icon="el-icon-user-solid" type="password" style="width: 100%" v-model="modifyForm.oldPassword" placeholder="原密码"></el-input>
          </el-form-item>
          <el-form-item prop="newPassword" label="新密码">
            <el-input type="password" prefix-icon="el-icon-shopping-bag-2" style="width: 100%;" v-model="modifyForm.newPassword" placeholder="新密码"></el-input>
          </el-form-item>
          <el-form-item prop="reNewPassword" label="确认密码">
            <el-input type="password" prefix-icon="el-icon-shopping-bag-2" style="width: 100%;" v-model="modifyForm.reNewPassword" placeholder="确认密码"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click.stop="modPwd">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import BackTop from '../components/back-top/BackTop'
// import menuList from '../utils/menu'
export default {
  components: { BackTop },
  name: 'Index',
  data() {
    // 密码验证
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.modifyForm.reNewPassword !== '') {
          this.$refs.modifyForm.validateField('reNewPassword')
        }
        callback()
      }
    }
    // 确认密码
    const validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.modifyForm.newPassword) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      muenuId: '',
      // 左侧菜单数据
      menuList: [],
      // 是否折叠
      isCollapse: false,
      // 被激活的链接地址
      activePath: '',
      // 被激活的一级菜单地址
      activeMenu: '/sendGood',
      showStore: false,
      dialogVisible: false,
      userInfo: {},
      storeList: [],
      curIndex: 0,
      cityCode: '',
      cityStores: [],
      storeName: '全部店铺',
      queryForm: {
        storeName: '',
        total: 0,
        page: 1,
        size: 10
      },
      subTitle: [],

      // 更多
      editPassDialog: false,
      dialogVisible: false,
      // 密码
      modifyForm: {
        oldPassword: '',
        newPassword: '',
        reNewPassword: ''
      },
      changeFormRules: {
        oldPassword: [{ required: true, message: '请输入原密码', trigger: 'blur' }],
        newPassword: [{ validator: validatePass, trigger: 'blur' }],
        reNewPassword: [{ validator: validatePass2, trigger: 'blur' }]
      }
    }
  },

  computed: {},

  methods: {
    accountOff() {
      this.httpPost('/basic/employee/logout', '').then(ret => {
        this.$sessionData('remove', 'USER')
        this.$router.replace('/login')
      })
    },

    // 一级菜单打开获取url
    handleOpen(key, keyPath) {
      this.activeMenu = key
    },

    // 保存链接的激活状态
    saveNavState(itemPath, subItemPath) {
      // console.log(this.$route.path)
      // console.log(itemPath, subItemPath)
      this.$setData('activeMenu', itemPath)
      this.$setData('activePath', subItemPath)
      this.activeMenu = itemPath
      this.activePath = subItemPath
    },

    // 点击按钮，切换菜单的折叠与展开
    toggleCollapse() {
      this.isCollapse = !this.isCollapse
    },
    showSelect() {
      this.dialogVisible = true
    },
    editPass() {
      this.dialogVisible = false
      this.editPassDialog = true
      this.$nextTick(() => {
        this.$refs.modifyForm.resetFields()
      })
    },
    //修改密码
    modPwd() {
      this.$refs['modifyForm'].validate(valid => {
        if (valid) {
          const params = {
            oldPassword: this.modifyForm.oldPassword,
            newPassword: this.modifyForm.newPassword,
            reNewPassword: this.modifyForm.reNewPassword
          }
          this.httpPost('/basic/employee/modPassword', params)
            .then(res => {
              this.$message.success('修改成功')
              this.editPassDialog = false
              this.httpPost('/basic/employee/logout', params).then(ret => {
                this.$sessionData('remove', 'USER')
                this.$router.replace('/login')
              })
            })
            .catch(res => {
              return this.$message.error(res), false
            })
        } else {
          return false
        }
      })
    },
    bodyClick(e) {
      this.dialogVisible = false
    }
  },

  mounted() {
    const userInfo = this.$getUser()
    if (userInfo) {
      this.userInfo = userInfo
    }
    //   // console.log(userInfo)
    // this.userInfo.uname = '管理员admin'
    this.menuList = this.$sessionData('get', 'menuList')
    this.activeMenu = this.$getData('activeMenu') ? this.$getData('activeMenu') : '/sendGood'
    this.activePath = this.$getData('activePath')
  }
}
</script>
